function renderDangerously(data) {
  return { __html: data };
}

export function LandingPage(props) {

  return (
    <>
      <div className="row">
        <div className="col">
          <h3>{props.title}</h3>
        </div>
      </div>
      <div className="row">
        <div className="col">
          <p dangerouslySetInnerHTML={renderDangerously(props.synopsis)}></p>
        </div>
      </div>
      <div className="row">
        <div className="col">
          <img src={props.picture} width="320px" />
        </div>
      </div>
      <div className="row p-3">
        <div className="col">
          <button
            type="button"
            className="btn btn-outline-dark btn-lg"
            onClick={() => props.setStart(true)}
          >
            {props.startButtonText}
          </button>
        </div>
      </div>
    </>
  );
}
