import { Container, Row, Col, Image, Card, Pagination, Button } from "react-bootstrap";
import { CSSTransition } from 'react-transition-group';

import { useParams, useLocation } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import React, { useState } from "react";
import { get_data } from "../data/get_data";
import { getPaginationItems } from "./Pagination"
import MoreLearning from "./MoreLearning"

import './styles.css';

function useQuery() {
  const { search } = useLocation();
  return React.useMemo(() => new URLSearchParams(search), [search]);
}

function renderDangerously(data) {
  return { __html: data };
}


export default function Topic(props) {
  const { topic_name } = useParams();
  let item = useQuery().get("item");
  let isHomePage = false;
  if (item == null) {
    isHomePage = true;
  }
  if (!Number.isInteger(parseInt(item))) {
    item = 1;
  }

  const data = get_data(props.chapter, topic_name);

  data.questions.forEach((q, id) => {
    q.id = id + 1
  })

  const paginationItems = getPaginationItems(data.questions, item);

  return (
    <Container fluid>
      <Row className="pb-5">
        <Col>
          {isHomePage ? (
            <TopicIntroPage
              title={data.quizTitle}
              synopsis={data.quizSynopsis}
              picture={data.quizPicture}
            />
          ) : (
              <TopicPage
                question={data.questions[item - 1]}
                nextItem={data.questions[item] === undefined ? null : parseInt(item) + 1}
                similarChapters={data.similarChapters}
              />
            )}
        </Col>
      </Row>
      {
        !isHomePage ? (<Row className="pt-5">
          <Col>
            <Pagination className="justify-content-center fixed-bottom" size="lg">
              {paginationItems}
            </Pagination>
          </Col>
        </Row>) : (<></>)
      }

    </Container>
  );
}

function TopicIntroPage(props) {
  return (
    <>
      <Row><Col><h5>{props.title}</h5></Col></Row>
      <Row><Col> <p dangerouslySetInnerHTML={renderDangerously(props.synopsis)}></p></Col></Row>
      <div className="row p-3">
        <div className="col">
          <img src={props.picture} className="w-100" style={{ maxWidth: "350px" }} />
        </div>
      </div>
      <Row className="justify-content-md-center">
        <Col xs={6}>
          <Button href="?item=1" variant="outline-dark" size="lg" block>–ù–∞—á–∞—Ç—å</Button>
        </Col>
      </Row>
    </>
  );
}

function TopicPage(props) {

  const [showQuestion, setshowQuestion] = useState(true);
  const [showAnswer, setshowAnswer] = useState(false);

  if (props.question.answerPic === "") {
    props.question.answerPic = props.question.questionPic;
  }

  function renderDangerously(data) {
    return { __html: data };
  }

  return (
    <>
      <Row>

        <Col xs={{ span: 12, order: 2 }} lg={{ span: 7, order: 1 }} >
          <>
            <Row><Col><h4>{props.question.question}</h4></Col></Row>
            <Row><Col>
              <ReactAudioPlayer src={props.question.questionAudio} controls />
            </Col></Row>
          </>

          <hr />
          {showQuestion && (
            <Row>
              <Col>
                <Button variant="outline-dark" size="lg" onClick={() => setshowAnswer(true)}>–£–∑–Ω–∞—Ç—å –û—Ç–≤–µ—Ç</Button>
              </Col>
            </Row>
          )}
          <CSSTransition in={showAnswer} timeout={0} classNames="learn-answer" unmountOnExit onEnter={() => setshowQuestion(false)} onExited={() => setshowQuestion(true)} >
            <>
              <Row><Col>
                <p dangerouslySetInnerHTML={renderDangerously(props.question.explanation)}></p>
              </Col></Row>
              <Row className="p-3"><Col><ReactAudioPlayer src={props.question.answerAudio} controls /></Col></Row>
              <Row><Col>
                {(props.nextItem != null) && (
                  <Button href={"?item=" + props.nextItem} variant="outline-dark" size="lg">–°–ª–µ–¥—É—é—â–∏–π</Button>
                )}

              </Col></Row>
            </>
          </CSSTransition>
        </Col>

        <Col xs={{ span: 12, order: 1 }} lg={{ span: 5, order: 2 }}>
          {showQuestion && (
            <QuestionMedia question={props.question} />
          )}

          <CSSTransition in={showAnswer} timeout={1300} classNames="learn-answer" unmountOnExit onEnter={() => setshowQuestion(false)} onExited={() => setshowQuestion(true)} >
            <AnswerMedia question={props.question} />
          </CSSTransition>
        </Col>
      </Row>

      {(props.nextItem == null && showAnswer) && (
            <MoreLearning />
      )}
    </>
  )
}

function QuestionMedia(props) {

  let videoSrcs = []
  if (props.question.questionVideo && Array.isArray(props.question.questionVideo)) {
    props.question.questionVideo.forEach((v, id) => {
      videoSrcs.push(
        <source key={id} src={v.src} type={v.type} />
      )
    })
  }

  return (
    <>
      <Image src={props.question.questionPic} rounded className="w-100" />
      {(props.question.questionVideo && props.question.questionVideo != undefined || props.question.questionVideo != "") &&
        (
          <video controls className="w-100">
            {videoSrcs}
                Sorry, your browser doesn't support embedded videos.
          </video>
        )}
    </>
  )
}

function AnswerMedia(props) {

  let videoSrcs = []
  if (props.question.answerVideo && Array.isArray(props.question.answerVideo)) {
    props.question.answerVideo.forEach((v, id) => {
      videoSrcs.push(
        <source key={id} src={v.src} type={v.type} />
      )
    })
  }

  let items = []
  if (Array.isArray(props.question.answerPic)) {
    props.question.answerPic.forEach((p, id) => {
      items.push(
        <Image key={id} src={p} rounded className="w-100" />
      )
    })
  } else {
    items.push(
      <Image key={1} src={props.question.answerPic} rounded className="w-100" />
    )
  }
  return (
    <>
      {items}
      {(props.question.questionVideo && props.question.questionVideo != undefined || props.question.questionVideo != "") &&
        (
          <video controls className="w-100">
            {videoSrcs}
                Sorry, your browser doesn't support embedded videos.
          </video>
        )}
    </>
  )
}