import { quiz_questions as c001_t004 } from "./chapter_001/c001_t004"
import { quiz_questions as c001_t005 } from "./chapter_001/c001_t005"

import { quiz_questions as c002_t001 } from "./chapter_002/c002_t001"
import { quiz_questions as c002_t002 } from "./chapter_002/c002_t002"

import { quiz_questions as c003_t001 } from "./chapter_003/c003_t001"
import { quiz_questions as c003_t002 } from "./chapter_003/c003_t002"


let allItems = [
    c001_t001,
    c001_t002,
    c001_t003,
    c001_t004,
    c001_t005,

    c002_t001,
    c002_t002,

    c003_t001,
    c003_t002,
]


export function RandomItems(n) {

    for (let i = allItems.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [allItems[i], allItems[j]] = [allItems[j], allItems[i]];
    }

    return allItems.slice(0,n);
}