import { RandomItems } from "../data/chapters/AllItems";

export function MoreLearning(props) {

    const randomItems = RandomItems(3);

    if (randomItems === undefined) {
        return (<></>)
    }

    const items = randomItems.map((item, i) => {
        return (
            <div className="col" key={i}>
                <a href= {"/learning"+item.selfLink}>
                    <div className="card">
                        <img src={item.quizPicture} className="card-img-top" width="100%" />
                        <div className="card-body">
                            <h5 className="card-title">{item.quizTitle}</h5>
                        </div>
                    </div>
                </a>
            </div>
        )
    })
    return (
        <>
            <div className="row justify-content-md-center pt-5">
                <div className="col col-8 ">
                    <h3>–í–∞–º —Ç–∞–∫–∂–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø–æ–ª–µ–∑–Ω–æ</h3>
                    <div className="row row-cols-1 row-cols-md-3 g-4 pt-3">
                        {items}
                    </div>
                </div>
            </div>

        </>
    )

}

export default MoreLearning;
