import { Pagination } from "react-bootstrap";


export function getPaginationItems(questions, item) {
    if (item==NaN || parseInt(item) == NaN){
        item = 1
    }

    let ids = questions.map((q) => {
        return q.id
    })
    let filteredIDs = centerOn(ids, parseInt(item), 5);
    let filteredQuestions = questions.map((q) => {
        if (filteredIDs.includes(q.id)) {
            return q
        }
    })
    let filtered = filteredQuestions.filter(function (x) {
        return x !== undefined;
    });

    const paginationItems = [];
    paginationItems.push(
        <Pagination.First key="first" href={"?item=" + questions[0].id}>
            {questions[0].id}
        </Pagination.First>
    )

    let prev = (parseInt(item) - 1);
    if (prev <= questions[0].id) {
        prev = questions[0].id;
    }
    paginationItems.push(<Pagination.Prev key="prev" href={"?item=" + prev} />)

    filtered.forEach((q, qid) => {
        paginationItems.push(
            <Pagination.Item
                key={qid}
                active={q.id === parseInt(item)}
                href={"?item=" + q.id}
            >
                {q.id}
            </Pagination.Item>
        )
    })

    let next = (parseInt(item) + 1);
    if (next > questions[questions.length - 1].id) {
        next = questions[questions.length - 1].id;
    }
    paginationItems.push(<Pagination.Next key="next" href={"?item=" + next} />)
    paginationItems.push(
        <Pagination.Last key="last" href={"?item=" + questions[questions.length - 1].id}>
            {questions[questions.length - 1].id}
        </Pagination.Last>
    )


    return paginationItems
}

function centerOn(arr, i, size) {
    let returnItems = []
    let n = arr.indexOf(i) - Math.floor(size / 2);

    while (n < 0) {
        n++
    }
    while (n + size > arr.length) {
        n--;
    }
    while (returnItems.length < size) {
        returnItems.push(arr[n]);
        n++;
    }
    let filtered = returnItems.filter(function (x) {
        return x !== undefined;
    });
    return filtered;
}