import React, { useState } from "react";
import { LandingPage } from "./LandingPage";
import { DisplayQuestion } from "./DisplayQuestion";
import { DisplayAnswer } from "./DisplayAnswer";
import { ResultsPage } from "./ResultsPage";

export default Quiz;

export function Quiz(props) {
  const [start, setStart] = useState(false);

  if (props.shuffle) {
    for (let i = props.quiz.questions.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      let temp = props.quiz.questions[i];
      props.quiz.questions[i] = props.quiz.questions[j];
      props.quiz.questions[j] = temp;
    }
  }

  return (
    <>
      {!start && (
        <LandingPage
          setStart={setStart}
          title={props.quiz.quizTitle}
          synopsis={props.quiz.quizSynopsis}
          picture={props.quiz.quizPicture}
          startButtonText={props.quiz.appLocale.startQuizBtn}
        />
      )}
      {start && <Questions questions={props.quiz.questions} />}
    </>
  );
}

function Questions(props) {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const isFinished = (currentQuestion == props.questions.length);
  const isLast = (currentQuestion + 1 == props.questions.length);

  return (
    <>
      {!isFinished && (
        <Question
          currentQuestion={currentQuestion}
          setCurrentQuestion={setCurrentQuestion}
          question={props.questions[currentQuestion]}
          isLast={isLast}
        />
      )}
      {isFinished && (
        <ResultsPage
          questions={props.questions}
        />
      )}
    </>
  );
}

function Question(props) {

  if (props.question.answerPic === "" || props.question.answerPic === undefined) {
    props.question.answerPic = props.question.questionPic;
  }

  const [displayQuestion, setDisplayQuestion] = useState(true);
  const [displayAnswer, setDisplayAnswer] = useState(false);

  return (
    <>
      {displayQuestion && (
        <DisplayQuestion
          question={props.question}
          setDisplayQuestion={setDisplayQuestion}
          setDisplayAnswer={setDisplayAnswer}

        />
      )}
      {displayAnswer && (
        <DisplayAnswer
          question={props.question}
          setDisplayQuestion={setDisplayQuestion}
          setDisplayAnswer={setDisplayAnswer}
          currentQuestion={props.currentQuestion}
          setCurrentQuestion={props.setCurrentQuestion}
          userAnswer={props.question.userAnswer}
          setCorrectCount={props.setCorrectCount}
          correctCount={props.correctCount}
          isLast={props.isLast}
        />
      )}
    </>
  );
}
