import { Row, Col, Image } from "react-bootstrap";
import ReactAudioPlayer from "react-audio-player";

function renderDangerously(data) {
    return { __html: data };
}

export function DisplayAnswer(props) {

    function nextQuestion() {
        props.setCurrentQuestion(props.currentQuestion + 1);
        props.setDisplayAnswer(false);
        props.setDisplayQuestion(true);
    }

    const correctAnswer = parseInt(props.question.correctAnswer) - 1;
    const userAnswer = props.userAnswer;
    const userCorrect = (correctAnswer === userAnswer);

    if (userCorrect) {
        props.question.userCorrect = true;
    }

    const answers = props.question.answers.map((answer, aid) => {
        let classes = "list-group-item list-group-item-action"
        if (userCorrect) {
            if (aid === userAnswer) {
                classes = classes + " list-group-item-success"
            }
        } else {
            if (aid === userAnswer) {
                classes = classes + " list-group-item-danger"
            }
            if (aid === correctAnswer) {
                classes = classes + " list-group-item-success"
            }
        }

        return (
            <li key={aid} className={classes} disabled>
                {answer}
            </li>
        );
    });

    return (
        <>
            <Row>
                <Col xs={{ span: 12, order: 2 }} lg={{ span: 7, order: 1 }} >
                    <>
                        <Row><Col><h4>{props.question.question}</h4></Col></Row>
                        <Row><Col><ReactAudioPlayer src={props.question.questionAudio} controls /></Col></Row>
                    </>
                    <hr />
                    <Row>
                        <Col>
                            <ul>{answers}</ul>
                        </Col>
                    </Row>

                    <Row className="p-3">
                        <Col>
                            <button
                                type="button"
                                className="btn btn-outline-dark btn-lg"
                                onClick={() => nextQuestion()}
                            >
                                {(props.isLast) ? "–†–µ–∑—É–ª—å—Ç–∞—Ç –¢–µ—Å—Ç–∞" : "–°–ª–µ–¥—É—é—â–∏–π"}
                            </button>
                        </Col>

                    </Row>

                    <Row>
                        <Col>
                            <Explanation
                                userCorrect={userCorrect}
                                explanation={props.question.explanation}
                                answerAudio={props.question.answerAudio}
                            />
                        </Col>
                    </Row>
                </Col>

                <Col xs={{ span: 12, order: 1 }} lg={{ span: 5, order: 2 }}>
                    <AnswerPictures answerPic={props.question.answerPic} />
                </Col>
            </Row>
        </>
    );
}

function Explanation(props) {

    let classes = ""
    if (props.userCorrect) {
        classes = "card mb-3 text-white bg-success"
    } else {
        classes = "card mb-3 text-white bg-danger"
    }

    return (
        <div className={classes}>
            <div className="card-body">
                <p className="card-text" dangerouslySetInnerHTML={renderDangerously(props.explanation)}></p>
                <ReactAudioPlayer src={props.answerAudio} controls />
            </div>
        </div>
    )
}

function AnswerPictures(props) {
    let items = []
    if (Array.isArray(props.answerPic)) {
      props.answerPic.forEach((p, id) => {
        items.push(
          <Image key={id} src={p} rounded className="w-100" />
        )
      })
    }else{
      items.push(
        <Image key={1} src={props.answerPic} rounded className="w-100" />
      )
    }
  
    return (
      <>
        {items}
      </>
    )
  }