import { useRouteMatch } from "react-router-dom";
import { Accordion, Card, ListGroup, Button } from "react-bootstrap";


const Menu = [
  {
    title: "–û–†–ò–ï–ù–¢–ò–†–û–í–ê–ù–ò–ï –í –ü–†–û–°–¢–†–ê–ù–°–¢–í–ï",
    url_target: "/chapter/c001",
    topics: [
      { title: "–ü–æ –∫–æ–º–ø–∞—Å—É, –∑–≤–µ–∑–¥–∞–º, –≤–µ—Ç—Ä—É", url_target: "/topic/t001" },
      { title: "–ü–æ —Å–æ–ª–Ω—Ü—É (–õ—É–Ω–µ) –∏ —á–∞—Å–∞–º", url_target: "/topic/t002" },
      { title: "–ü–æ —Ä–∞—Å—Ç–µ–Ω–∏—è–º", url_target: "/topic/t003" },
      { title: "–ü–æ –º–µ—Å—Ç–Ω—ã–º –ø—Ä–∏–∑–Ω–∞–∫–∞–º", url_target: "/topic/t004" },
      { title: "–û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–π –∏ —Ä–∞–∑–º–µ—Ä–æ–≤", url_target: "/topic/t005" },
    ],
  },
  {
    title: "–£–ú–ï–ï–¢–ï –õ–ò –í–´ –ß–ò–¢–ê–¢–¨ –°–õ–ï–î–´?",
    url_target: "/chapter/c002",
    topics: [
      { title: "–°–ª–µ–¥—ã —á–µ–ª–æ–≤–µ–∫–∞", url_target: "/topic/t001" },
      { title: "–°–ª–µ–¥—ã –∑–≤–µ—Ä–µ–π", url_target: "/topic/t002" },
    ],
  },
  {
    title: "–û–†–ò–ï–ù–¢–ò–†–û–í–ê–ù–ò–ï –í –†–ê–°–¢–ò–¢–ï–õ–¨–ù–û–ú –ú–ò–†–ï",
    url_target: "/chapter/c003",
    topics: [
      { title: "–ú–∏—Ä —Ä–∞—Å—Ç–µ–Ω–∏–π...", url_target: "/topic/t001" },
      { title: "–ú–∏—Ä –≥—Ä–∏–±–æ–≤...", url_target: "/topic/t002" },
    ],
  },
];

export function DataMenu() {

  const { url } = useRouteMatch();

  const chapters = Menu.map((chapter, chapter_id) => {
    const eventKey = '"' + chapter_id + '"';
    const chapter_url_target = chapter.url_target;

    const topics = chapter.topics.map((topic, topic_id) => {
      const topic_target = url + chapter_url_target + topic.url_target;
      return (
        <a key={topic_id} href={topic_target} className="list-group-item list-group-item-action">{topic.title}</a>
      );
    });

    return (
      <Card key={chapter_id}>
        <Accordion.Toggle as={Button} variant="light outline-dark" eventKey={eventKey}>
          {chapter.title}
        </Accordion.Toggle>
        <Accordion.Collapse eventKey={eventKey}>
          <ListGroup>{topics}</ListGroup>
        </Accordion.Collapse>
      </Card>
    );
  });

  return <Accordion className="pb-4">{chapters}</Accordion>;
}

export default DataMenu;