
function renderDangerously(data) {
  return { __html: data };
}

function ResultCard(props) {

  const answers = props.question.answers.map((answer, aid) => {
    let classes = "list-group-item list-group-item-action"
    if (props.question.userCorrect) {
      if (aid === parseInt(props.question.correctAnswer) - 1) {
        classes = classes + " list-group-item-success"
      }
    } else {
      if (aid === props.question.userAnswer) {
        classes = classes + " list-group-item-danger"
      }
      if (aid === parseInt(props.question.correctAnswer) - 1) {
        classes = classes + " list-group-item-success"
      }
    }

    return (
      <li key={aid} className={classes} disabled>
        {answer}
      </li>
    );
  });


  return (
    <>
      <div className="col-12">
        <div className="card mb-5">
          <div className="row">
            <div className="col-md-8">
              <div className="card-body">
                <h5 className="card-title">{props.question.question}</h5>
                <ul className="list-group list-group-flush">
                  {answers}
                </ul>
              </div>
            </div>
            <div className="col-md-4">
              <img src={props.question.questionPic} width="100%" />
            </div>
          </div>
          <div className="row">
            <div className="col">
              <div className="card-footer bg-transparent">
                <p className="card-text" dangerouslySetInnerHTML={renderDangerously(props.question.explanation)}></p>
              </div>
            </div>
          </div>


        </div>
      </div>
    </>

  )
}

export function ResultsPage(props) {
  let correctCount = 0;
  props.questions.forEach((q) => {
    if (q.userCorrect) {
      correctCount++;
    }
  })

  const questions = props.questions.map((question, qid) => {
    return <ResultCard key={qid} question={question} />
  })

  return (
    <>
      <div className="row">
        <div className="col">
          <h3>–£—Ä–∞! –¢–µ—Å—Ç –∑–∞–≤–µ—Ä—à–µ–Ω</h3>
          <h3>–í–∞—à —Ä–µ–∑—É–ª—å—Ç–∞—Ç: {correctCount}/{props.questions.length}</h3>
        </div>
      </div>

      <div className="row">
        {questions}
      </div>

    </>
  );
}